<template>
	<div>
		<a-modal title="我的资料" :visible="visible" :confirm-loading="confirmLoading" @ok="handleOk" @cancel="handleCancel"
		 @afterClose="handleCancel">
			<a-form-model :label-col="labelCol" :wrapper-col="wrapperCol">
				<div style="display: flex; justify-content: space-around;">
					<a-form-model-item  label="">
						<a-upload name="file" list-type="picture-card" class="poster-uploader-adminInfo" :show-upload-list="false" :action="this.$store.state.requestPath + 'uploadHeadPic'"
						 :before-upload="beforeUpload" @change="handleChange">
							<a-avatar v-if="imageUrl" :size=100 :src="imageUrl"/>
							<!-- <img v-if="imageUrl" :src="imageUrl" style="width: 100px; height: 100px;" /> -->
							<div v-else>
								<a-icon :type="picloading ? 'loading' : 'plus'" />
								<div class="ant-upload-text">
									上传
								</div>
							</div>
						</a-upload>
					</a-form-model-item>
					<div>
						<a-form-model-item label="用户名">
							<a-input v-model="record.name" :disabled="true" class="admin-info" />
						</a-form-model-item>
						<a-form-model-item label="邮箱">
							<a-input v-model="record.email" :disabled="!modify" class="admin-info" />
						</a-form-model-item>
						<a-form-model-item label="修改开关">
							<a-switch v-model="modify" style="margin-left: 30px;" />
						</a-form-model-item>
					</div>
					
				</div>
			</a-form-model>
		</a-modal>
	</div>
</template>
<script>
	import {
		mapMutations,
		mapState
	} from 'vuex';
	function getBase64(img, callback) {
		const reader = new FileReader();
		reader.addEventListener('load', () => callback(reader.result));
		reader.readAsDataURL(img);
	};
	export default {
		computed: {
			...mapState(['userInfo', 'requestPath', 'picRequestPath'])
		},
		props: ['info', 'uid'],
		async created() {
			this.visible = this.info;
			let {
				data
			} = await this.$get(this.$store.state.requestPath + 'admin/search/' + this.uid);
			this.record = data.data;
			this.imageUrl = this.picRequestPath + 'headpic/' + this.record.headPic;
			this.latestImageUrl = this.imageUrl;
		},
		data() {
			return {
				labelCol: {
					span: 4
				},
				wrapperCol: {
					span: 14
				},
				labelCol: {
					span: 4
				},
				wrapperCol: {
					span: 14
				},
				picloading: false,
				modify: false,
				visible: false,
				imageUrl: '',
				latestImageUrl: null,
				confirmLoading: false,
				record: {
					uid: this.uid,
					name: null,
					email: null,
				},
			};
		},
		methods: {
			async handleOk(e) {
				this.confirmLoading = true;
				if(this.modify){
					let {
						data
					} = await this.axios.post(this.$store.state.requestPath + 'admin/update' , this.record);
					if(data.code == 0){
						this.$message.success("修改成功");
					}else{
						this.$message.error("修改失败");
					}
				}
				setTimeout(() => {
					this.visible = false;
					this.$emit("closeInfoWindow");
					this.confirmLoading = false;
				}, 1000);
			},
			handleCancel() {
				this.modify = false;
				this.record.headPic = this.latestImageUrl;
				this.imageUrl = this.latestImageUrl;
				this.visible = false;
				this.$emit("closeInfoWindow")
			},
			handleChange(info) {
				if (info.file.status === 'uploading') {
					this.picloading = true;
					return;
				}
				if (info.file.status === 'done') {
					// 从前端获取上传的 img-url
					getBase64(info.file.originFileObj, imageUrl => {
						this.imageUrl = imageUrl;
						this.picloading = false;
					});
					this.record.headPic = info.file.response.data.src;
				}
			},
			beforeUpload(file) {
				const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
				if (!isJpgOrPng) {
					this.$message.error('You can only upload JPG file!');
				}
				const isLt2M = file.size / 1024 / 1024 < 2;
				if (!isLt2M) {
					this.$message.error('Image must smaller than 2MB!');
				}
				return isJpgOrPng && isLt2M;
			},
		},
	};
</script>

<style>
	.admin-info {
		margin-left: 30px;
		width: 200px;
	}

	.poster-uploader-adminInfo>.ant-upload {
		border-radius: 50%;
	}

	.ant-upload-select-picture-card i {
		font-size: 16;
		color: #999;
	}

	.ant-upload-select-picture-card .ant-upload-text {
		margin-top: 8px;
		color: #666;
	}
</style>
