<template>
	<div>
		<a-modal title="修改密码" :visible="visible" :confirm-loading="confirmLoading" @ok="handleOk" @cancel="handleCancel"
		 @afterClose="handleCancel">
			<a-form :form="form" :label-col="labelCol" :wrapper-col="wrapperCol">
				<a-form-item label="原密码">
					<a-input v-decorator="[
					  'oldpwd',
					  { rules: [{ required: true, message: '原密码不能为空' }] },
					]" type="password"
					 placeholder="请输入原密码" />
				</a-form-item>
				<a-form-item label="新密码">
					<a-input v-decorator="[
					  'newpwd1',
					  { rules: [{ required: true, message: '新密码不能为空' }] },
					]" type="password"
					 placeholder="请输入新密码" />
				</a-form-item>
				<a-form-item label="再次输入">
					<a-input v-decorator="[
					  'newpwd2',
					  { rules: [{ required: true, message: '不能为空' }] },
					]" type="password"
					 placeholder="请输入新密码" />
				</a-form-item>
			</a-form>
		</a-modal>
	</div>
</template>

<script>
	import {
		mapMutations,
		mapState
	} from 'vuex';
	export default {
		computed: {
			...mapState(['userInfo', 'requestPath'])
		},
		props: ['pwd'],
		created() {
			this.visible = this.pwd;
		},
		data() {
			return {
				labelCol: {
					span: 4
				},
				wrapperCol: {
					span: 14
				},
				labelCol: {
					span: 4
				},
				wrapperCol: {
					span: 14
				},
				form: this.$form.createForm(this),
				visible: false,
				confirmLoading: false,
			};
		},
		methods: {
			async handleOk(e) {
				e.preventDefault();
				this.form.validateFields(async (err, values) => {
					if (!err) {
						console.log(this.userInfo)
						if (values.oldpwd == this.userInfo.password) {
							if (values.newpwd1 == values.newpwd2) {
								this.userInfo.password = values.newpwd1
								let {
									data
								} = await this.axios.post(this.$store.state.requestPath + 'admin/updatePwd' , this.userInfo);
								if (data.code == 0) {
									this.$message.success("修改成功")
									this.$message.success("请重新登录~~")
									this.$router.push("/")
								} else {
									this.$message.error("修改失败")
									return;
								}
							} else {
								this.$message.error("两次密码不一致")
								return;
							}
						} else {
							this.$message.error("原密码不正确")
							return;
						}
						this.confirmLoading = true;
						setTimeout(() => {
							this.visible = false;
							this.$emit("closeResetPwdPage");
							this.confirmLoading = false;
						}, 1000);
					}
				});
			},
			handleCancel() {
				this.visible = false;
				this.$emit("closeResetPwdPage")
			},
		},
	};
</script>

