<script>
	import AdminInfo from "./AdminInfo.vue";
	import ResetPwdPage from "./ResetPwdPage.vue"
	import {
		mapMutations,
		mapState
	} from "vuex";
	export default {
		components: {
			AdminInfo,
			ResetPwdPage
		},
		async created() {
			if (this.userInfo != null) {
				let {
					data
				} = await this.$get(this.$store.state.requestPath + '/admin/menu/' + this.userInfo.uid);
				this.menu = data;
				console.log(data);
			}
		},
		computed: {
			...mapState(['requestPath', 'userInfo', 'picRequestPath']),
			titles() {
				var titles = [];
				titles.push('首页');
				if (this.$route.meta.model) {
					titles.push(this.$route.meta.model)
				}
				if (this.$route.meta.title) {
					titles.push(this.$route.meta.title)
				}
				return titles;
			},
		},
		data() {
			return {
				info: false,
				pwd: false,
				collapsed: false,
				menu: null,
			};
		},
		methods: {
			...mapMutations(['logout']),
			userLogout() {
				this.logout();
				this.$router.push({
					path: '/'
				});
				this.$message.success(' (￣︶￣)↗ﾞ 感谢您的使用~ ', 2);
			},
			openInfoWindow() {
				this.info = true;
			},
			closeInfoWindow() {
				this.info = false;
			},
			openRewtPwdWindow() {
				this.pwd = true;
			},
			closeResetPwdPage() {
				this.pwd = false;
			}
		}
	};
</script>
<style lang="scss">
	#components-layout-demo-custom-trigger {
